import React from 'react';
import '@patternfly/react-core/dist/styles/base.css';
import { useState } from 'react';
import {
  Card,
  CardTitle,
  CardBody,
  CardFooter,
  Switch,
  Label,
  Tooltip,
} from '@patternfly/react-core';

import './app.css';

function App() {
  const [isRounded, setIsRounded] = useState(false);
  const [isCompact, setIsCompact] = useState(false);
  return (
    <Card isRounded={isRounded} isCompact={isCompact}>
      <CardTitle>PatternFly Elements</CardTitle>
      <CardBody>
        A react design system, made with
        <Label color="blue">☕</Label> and
        <Tooltip content={ "JavaScript-Heavy, vendor-locked, non-standard component model" }>
          <Label color="orange">React-DOM</Label>
        </Tooltip>
      </CardBody>
      <CardFooter>
        <Switch label="Rounded?" onChange={() => setIsRounded(!isRounded)}></Switch>
        <Switch label="Compact?" onChange={() => setIsCompact(!isCompact)}></Switch>
      </CardFooter>
    </Card>
  );
}

export default App;
